
import { Component, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class TextInput extends Vue {
  inheritAttrs = false;

  @Prop({
    default: null,
  })
  readonly value!: string | number;

  @Prop({
    default: 'text',
  })
  readonly type!: string;

  @Prop({
    default: '',
  })
  readonly label!: string;

  @Prop({
    default: '',
    required: true,
  })
  readonly id!: string;

  @Prop({
    default: '',
  })
  readonly classes!: string;

  @Prop({
    default: null,
  })
  readonly min!: number;

  @Prop({
    default: null,
  })
  readonly max!: number;

  @Prop({ default: '' })
  placeholder!: string;

  updateValue(newVal: string): void {
    this.$emit('input', newVal);
  }

  onClear(): void {
    this.updateValue('');
  }
}
